    await loadOrderList()
    updateStatusCounts()
  } catch (error) {
    console.error('Âà∑Êñ∞Â§±Ë¥•:', error)
  } finally {
    isRefreshing.value = false
  }
}

const onRefreshRestore = () => {
  isRefreshing.value = false
}

// Âä†ËΩΩÊõ¥Â§ö
const loadMore = async () => {
  if (isLoadingMore.value || !hasMore.value) return
  
  isLoadingMore.value = true
  page.value++
  
